.checkout-panel {
  width: 100%;
  min-height: 100vh;
  // overflow: hidden;
  padding: 0.9375rem;
  padding-top: 3.75rem;
  background: #ff8220;

  $boxShadow1: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.12), 0 0.0625rem 0.125rem rgba(0, 0, 0, 0.24);
  $boxShadow2: 0 0.125rem 0.3125rem rgba(0, 0, 0, 0.16), 0 0.125rem 0.3125rem rgba(0, 0, 0, 0.13);
  $primaryFontSize: 0.75rem;
  $primaryBorderRadius: 0.625rem;
  $primaryBgColor: #f8f8f8;

  .checkout-panel__main-board {
    width: 100%;
    height: 90.5vh;
    background: $primaryBgColor;
    box-shadow: $boxShadow2;
    border-radius: $primaryBorderRadius;
    overflow: hidden;
    display: flex;

    .ant-row {
      height: 100%;
    }

    .checkout-panel__main-board__navbar {
      padding-top: 1.25rem;

      .checkout-panel__main-board__navbar__item {
        width: 2.5rem;
        height: 2.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        margin-left: 0.9375rem;
        margin-top: 0.625rem;
        margin-bottom: 1.25rem;
        background: white;

        &.checkout-panel__main-board__navbar__item--selected {
          background: darkorange !important;
          box-shadow: 0rem 0.0625rem 0.1875rem rgba($color: #000000, $alpha: 0.4);

          .checkout-panel__main-board__navbar__item__icon {
            color: white !important;
          }
        }

        .checkout-panel__main-board__navbar__item__icon {
          margin-top: 0.125rem;
        }
      }

      .ant-menu-dark.ant-menu-inline,
      .ant-menu-dark.ant-menu-vertical,
      .ant-menu-dark.ant-menu-vertical-left,
      .ant-menu-dark.ant-menu-vertical-right {
        height: 100%;
        width: 3.75rem;
        background: $primaryBgColor;

        .ant-menu-item {
          padding: 0.125rem 0 0 0 !important;
          box-sizing: border-box;

          .anticon {
            color: rgba($color: #000000, $alpha: 0.3);
          }

          &.ant-menu-item-selected {
            background: none;
          }
        }
      }
    }
  }

  .checkout-panel__main-board__content {
    width: 100%;
    height: 100%;
    padding: 0.625rem;
    background: $primaryBgColor;
  }
}

@media screen and (max-width: 768px) {
  .checkout-panel {
    padding: 5.5em 0.5em 0.5em 0.5em;
  }

  .checkout-panel .checkout-panel__main-board {
    flex-direction: column;
  }

  .checkout-panel .checkout-panel__main-board .checkout-panel__main-board__navbar {
    height: 5rem;
  }

  .checkout-panel .checkout-panel__main-board .checkout-panel__main-board__navbar .ant-menu-dark.ant-menu-inline,
  .checkout-panel .checkout-panel__main-board .checkout-panel__main-board__navbar .ant-menu-dark.ant-menu-vertical,
  .checkout-panel .checkout-panel__main-board .checkout-panel__main-board__navbar .ant-menu-dark.ant-menu-vertical-left,
  .checkout-panel
    .checkout-panel__main-board
    .checkout-panel__main-board__navbar
    .ant-menu-dark.ant-menu-vertical-right {
    display: flex;
    flex-direction: row;
    width: 100%;
  }
}
